.main {
  height: 100vh;
  display: grid;
  grid-template-rows: 10vw 1fr 1fr 1fr 0.5fr 1fr 1fr 1fr 10vw;
  grid-template-columns: 10vw 1fr 1fr 1fr 10vw;
  place-items: stretch;
}

.farbeH {
  grid-row: 2;
  grid-column-start: 2;
  grid-column-end: 5;
}

.farbeS {
  grid-row: 3;
  grid-column-start: 2;
  grid-column-end: 5;
}

.farbeL {
  grid-row: 4;
  grid-column-start: 2;
  grid-column-end: 5;
}

.info {
  grid-row: 1;
  grid-column: 5;
  place-self: center;
  color: red;
  font-weight: bold;
  font-size: 3vw;
}

.slider {
width: 100%;
height: 60px;
border-radius: 5px;
border-color: white;
border-width: 3px;
border-style: solid;
}

.slider::-moz-range-track {
  background: transparent;
  height: 50px
}

.slider::-moz-range-thumb {
  height: 60px
}

.text {
  margin-left: 5px;
  font-weight: bold;
}

.button {
  margin: 1.5vh;
  border-radius: 5px;
  border-color: white;
  border-width: 3px;
  border-style: solid;
  color: #ffffff;
  box-shadow: 1px 1px 10px 1px black;
}

.favorit {
  background: white;
  color: black;
}

.spinner {
  height:7vw;
  width:7vw;
  margin:0px auto;
  animation: rotation .6s infinite linear;
  border-left:1vw solid rgba(0,174,239,.15);
  border-right:1vw solid rgba(0,174,239,.15);
  border-bottom:1vw solid rgba(0,174,239,.15);
  border-top:1vw solid rgba(0,174,239,.8);
  border-radius:100%;
}

@keyframes rotation {
  from {transform: rotate(0deg);}
  to {transform: rotate(359deg);}
}